.wp-block-block-course-team-members {
	display: block;
	@for $i from 1 through 6 {
		&.has-#{$i}-columns {
			> .block-editor-inner-blocks > .block-editor-block-list__layout > [data-type="block-course/team-member"] {
				width: calc(100% / #{$i} - 40px);
			}

			.block-list-appender {
				align-self: stretch;
				width: calc(97% / #{$i});
				border: 1px dashed;
				display: flex;
				align-items: center;
				justify-content: center;
				min-height: 70px;
				position: relative;
			}
		}
	}

	> .block-editor-inner-blocks > .block-editor-block-list__layout {
		display: flex;
		flex-wrap: wrap;
		gap: 10px;

		[data-type="block-course/team-member"] {
			max-width: none;
			margin: 0;
		}

		> [data-type="block-course/team-member"] {
			padding: 15px;
			box-shadow: 0 0 3px rgba(0, 0, 0, 0.5);
			text-align: center;

			.wp-block-block-course-team-members-img {
				position: relative;

				&.is-loading img {
					opacity: 0.3;
				}

				.components-spinner {
					position: absolute;
					left: 0;
					right: 0;
					top: 0;
					bottom: 0;
					margin: auto;
				}
			}

			h4 {
				margin: 10px 0 0 0;
				text-transform: uppercase;
				font-weight: bold;
				line-height: 100%;
			}

			p {
				margin: 0;
				font-style: italic;
				font-weight: lighter;
			}

			.wp-block-block-course-team-members-social-links {
				ul {
					list-style: none;
					padding: 0;
					margin: 10px 0 0 0;

					li {
						display: inline-block;
						margin: 5px;

						span.dashicon {
							color: #4e4e4e;
						}

						&.is-selected {
							outline: 2px solid rgba(0, 0, 0);
						}

						button {
							background: none;
							border: none;
							padding: 0;
							cursor: pointer;
							margin: 0;
						}

						&.wp-block-block-course-team-members-add-icon-li {
							button {
								outline: 1px dashed;
							}
						}
					}
				}
			}

			.wp-block-block-course-team-members-link-form {
				background: #fff;
				border: 1px solid;
				padding: 10px;
				margin-top: 10px;
			}
		}

	}

}
